import React from 'react';
import Table from '../../../../components/Table';
import { calculationResultTableHeaders } from '../../../../models/calculationResult';

const deserializeValues = data =>
  data.map(row => ({
    ...row,
    samples: row.samples.length,
    malesAverage: row.malesAverage.toFixed(2),
    femalesAverage: row.femalesAverage.toFixed(2),
    SDI: row.SDI.toFixed(2),
  }))

const CalculationResultTable = ({ result }) =>
  <Table
    title="Resultado do cálculo do SDI"
    columns={calculationResultTableHeaders}
    data={deserializeValues(result)}
    options={{
      exportButton: true
    }}
  />

export default CalculationResultTable;
